import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { format } from 'date-fns';
import { ChevronRightIcon, ClockIcon, CheckCircleIcon, ExclamationIcon } from '@heroicons/react/solid';
import apiEndpoints from '../apiService';

const AssignmentList = () => {
  const [assignments, setAssignments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchAssignments = async () => {
      try {
        const data = await apiEndpoints.assignments.getAll();
        setAssignments(data);
        setLoading(false);
      } catch (err) {
        setError('Failed to fetch assignments. Please try again later.');
        setLoading(false);
      }
    };

    fetchAssignments();
  }, []);

  const getStatusIcon = (status) => {
    switch (status) {
      case 'completed':
        return <CheckCircleIcon className="w-6 h-6 text-green-500" />;
      case 'overdue':
        return <ExclamationIcon className="w-6 h-6 text-red-500" />;
      default:
        return <ClockIcon className="w-6 h-6 text-yellow-500" />;
    }
  };

  if (loading) {
    return <div className="flex justify-center items-center h-screen">Loading assignments...</div>;
  }

  if (error) {
    return <div className="text-red-500 text-center mt-4">{error}</div>;
  }

  return (
    <div className="max-w-4xl mx-auto mt-8 px-4">
      <h1 className="text-3xl font-bold mb-6">My Assignments</h1>
      <div className="bg-white shadow overflow-hidden sm:rounded-md">
        <ul className="divide-y divide-gray-200">
          {assignments.map((assignment) => (
            <li key={assignment.id}>
              <Link to={`/assignment/${assignment.id}`} className="block hover:bg-gray-50">
                <div className="flex items-center px-4 py-4 sm:px-6">
                  <div className="min-w-0 flex-1 flex items-center">
                    <div className="flex-shrink-0">
                      {getStatusIcon(assignment.status)}
                    </div>
                    <div className="min-w-0 flex-1 px-4 md:grid md:grid-cols-2 md:gap-4">
                      <div>
                        <p className="text-sm font-medium text-indigo-600 truncate">{assignment.title}</p>
                        <p className="mt-2 flex items-center text-sm text-gray-500">
                          <span className="truncate">{assignment.course}</span>
                        </p>
                      </div>
                      <div className="hidden md:block">
                        <div>
                          <p className="text-sm text-gray-900">
                            Due: {format(new Date(assignment.due_date), 'MMM dd, yyyy')}
                          </p>
                          <p className="mt-2 flex items-center text-sm text-gray-500">
                            {assignment.status === 'completed' ? 'Submitted' : 'Not submitted'}
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div>
                    <ChevronRightIcon className="h-5 w-5 text-gray-400" aria-hidden="true" />
                  </div>
                </div>
              </Link>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default AssignmentList;